.cards-wrapper {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 1840px) {
  .main {
    display: flex;
    justify-content: center;
  }
  .cards-wrapper {
    width: 1500px;
    margin: 0 auto;
  }
  .card {
    margin: 37px;
  }
}

@media screen and (max-width:1420px){
  .cards-wrapper {
    width: 1000px;
  }
}

@media screen and (max-width: 947px) {
  .cards-wrapper {
    justify-content: center;
  }
}
