.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 90px;
  background-color: rgb(233, 233, 233);
  z-index: 100;
}

.header-items {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

a {
  text-decoration: none;
  color: black;
}

.header-button {
  width: 150px;
  left: 75%;
  padding: 10px;
  background: rgb(175, 175, 175);
  cursor: pointer;
}
